	.simple-imageupload-preview {
		background-size: contain;
		background-repeat: no-repeat;
		background-position: center center;
		box-sizing: border-box;
		cursor: pointer;
		height: 100%;
		width: 100%;

		&.simple-imageupload-empty {
			@color: #cccccc;
			position: relative;

			&:before,
			&:after {
				box-sizing: border-box;
				content: '';
				display: block;
				position: absolute;
				left: 0;
				line-height: 1em;
				text-align: center;
				top: 50%;
				width: 100%;
			}
			&:after {
				color: @color;
				content: attr( data-placeholder );
				font-size: 1em;
				margin-top: -.5em;
			}
			&:before {
				color: lighten( @color, 18 );
